import { Component, OnInit, ViewChild } from '@angular/core';
import { PartParoduct } from '../../_models/PartParoduct';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { AlertifyService } from '../../_services/alertify.service';
import { ModalDirective } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-make-model',
  templateUrl: './make-model.component.html',
  styleUrls: ['./make-model.component.css']
})
export class MakeModelComponent implements OnInit {
  model: any = {};
  makes: PartParoduct[];
  models: PartParoduct[];
  roleid: any =  Number(localStorage.getItem('RoleID'));
  @ViewChild('makeModal') public makeModal: ModalDirective;
  @ViewChild('modelModal') public modelModal: ModalDirective;

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.loadMakes();
    this.loadModels();
  }
  loadMakes() {
    return this.loadspecsService.getMakes().subscribe((partproducts: PartParoduct[]) => {
      this.makes = partproducts;
    }, error => {
      this.alertify.error(error);
    });
  }
  loadModels() {
    return this.loadspecsService.getModels().subscribe((partproducts: PartParoduct[]) => {
      this.models = partproducts;
    }, error => {
      this.alertify.error(error);
    });
  }
  AddMake() {
    this.model.ParentId = 0;
    this.model.Type = 1;
    if (!this.model.ProductName) {
      this.alertify.error('Enter Make');
      return;
    }
    this.loadspecsService.AddMake(this.model).subscribe((partproducts: PartParoduct[]) => {
      this.makes = partproducts;
      this.alertify.success('Make Added Successful');
      this.makeModal.hide();
      this.model.ProductName = '';
    }, error => {
      this.alertify.error(error);
    });
  }
  AddModel() {
    this.model.Type = 2;
    if (!this.model.ParentId) {
      this.alertify.error('Select Make');
      return;
    }
    if (!this.model.ProductName) {
      this.alertify.error('Enter Model');
      return;
    }
    this.loadspecsService.AddModel(this.model).subscribe((partproducts: PartParoduct[]) => {
      this.models = partproducts;
      this.alertify.success('Model Added Successful');
      this.modelModal.hide();
      this.model.ProductName = '';
    }, error => {
      this.alertify.error(error);
    });
  }

}
